/* 确保容器相对定位 */
.avatar-container {
    position: relative;
    width: 100%;
}

/* 默认图像 */
.default-avatar {
    display: block; /* 确保默认显示 */
    width: 100%; /* 根据需要调整宽度 */
    transition: opacity 0.3s ease-in-out; /* 添加过渡效果 */
}

/* 悬停图像初始隐藏 */
.hover-avatar {
    position: absolute; /* 绝对定位，确保两张图像重叠 */
    top: 0;
    left: 0;
    width: 100%; /* 根据需要调整宽度 */
    opacity: 0; /* 初始状态下隐藏悬停图像 */
    transition: opacity 0.3s ease-in-out; /* 添加过渡效果 */
}

/* 悬停时隐藏默认图像 */
.avatar-container:hover .default-avatar {
    opacity: 0; /* 悬停时隐藏默认图像 */
}

/* 悬停时显示悬停图像 */
.avatar-container:hover .hover-avatar {
    opacity: 1; /* 悬停时显示悬停图像 */
}


#typed {
    white-space: pre-wrap;
    word-break: break-word;
}
.title{
    margin-top: 50px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.title h3{
    border-bottom: 3px solid #FF6666;
}

.img-container {
    position: relative;
    padding-bottom: 100%; /* 创建一个正方形容器 */
}

.img-container img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.card-hover {
  transition: background-color 0.3s; /* 添加过渡效果 */
}

.card-hover:hover {
  background-color: white; /* 鼠标移入时背景颜色为白色 */
  cursor: pointer;
}

</style>